body {
  margin: 0;
  padding: 0;
  background: #30E8BF;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FF8235, #30E8BF);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #FF8235, #30E8BF);
  background: linear-gradient(to right, #FF8235, #30E8BF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.search-bar {
    font-size: 2.4rem;
    width: 25rem;
    height: 5rem;
    border: none;
    outline: none;
    border-radius: 10rem;
    box-shadow: 10px 10px 18px grey;
    padding: 2rem;
    -webkit-transition: all .2s;
    transition: all .2s;
    -webkit-transition-delay: .1s;
            transition-delay: .1s;
}
.book_image {
    width:100px;
    box-shadow:10px 10px 18px grey;
    height: auto;
}
.book_container {
    width: 425px;
    border-radius: 20px;
    margin: 20px;
    box-shadow:10px 10px 18px grey;
}
.book_wrapper {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}
.book_details {
    margin-left: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.buy-button {
    width: 140px;
}
.search-bar_container {
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  padding-top: 100px;
  margin-bottom: 100px;
}
.button {
  height: 50px;
  -webkit-transition: all .2s;
  transition: all .2s;
  -webkit-transition-delay: .1s;
          transition-delay: .1s;
  margin-left: -70px;
  margin-top: 18px;
}
.search-result_container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
